let colors = {
    primary: '#2C77F4',
    green: '#30BFA3',
    yellow: '#FFDE8F',
    orange: '#FEB64E',
    red: '#FB5F8E',
    purple: 'A175F7',
    blue: '#5F6EEF',
    gray: '#CCCCCC',
    colorArray: [] as string[]
}

for (let key in colors) {
    if (key !== 'colorArray') {
        colors.colorArray.push((colors as any)[key])
    }
}

export default colors
