import Vue from '@/components/base'
import { Component, Prop } from 'vue-property-decorator'
import template from './VueMarkdown.vue'
import 'highlight.js/styles/github.css'

import highlight from 'highlight.js'

@Component({
    name: 'VueMarkdown',
    mixins: [template]
})
export default class VueMarkdown extends Vue {
    //#region datas ******************************/
    //#endregion datas ***************************/

    //#region props ******************************/
    @Prop()
    protected src!: string
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    protected mounted() {
        this.$el.querySelectorAll('code').forEach(block => {
            highlight.highlightBlock(block)
        })
    }
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    //#endregion event handler *******************/

    //#region methods ****************************/
    //#endregion methods *************************/
}
