import 'normalize.css'
import '@/style/lib.scss'

import { VueConstructor } from 'vue'
import chartConfig from '@/common/chart-config'

import Avatar from '@/components/Avatar'
import Image from '@/components/Image'
import Upload from '@/components/Upload'

let components = [Avatar, Image, Upload]

// Vue Plugin
function install(
    Vue: VueConstructor,
    options: { prefix: string } = { prefix: 'Yz' }
) {
    components.forEach(component => {
        // @ts-ignore
        let name = component.options ? component.options.name : component.name
        name = name.replace(/^Yz/, '')
        Vue.component(`${options.prefix}${name}`, component)
    })
}

// export 组件
export { Avatar, Image, Upload }

// 图表配置
export { chartConfig }

// export vue 插件
export default { install }
