import { getVarValue } from '@yzfe/utils/dist/dom'

// echarts 配置 utils
const utils = {
    variables: () => {
        let textRegular = getVarValue('--color-text-regular') || ''
        let textPrimary = getVarValue('--color-text-primary') || ''
        let fontFamily = getVarValue('--font-fmaily') || ''

        return {
            textRegular,
            textPrimary,
            fontFamily
        }
    },
    // 线图渐变
    lineAreaGradientColor: (
        colorStops?: { offset: number; color: string }[]
    ) => {
        if (!colorStops) {
            colorStops = [
                {
                    offset: 0,
                    color: 'rgba(242, 247, 255, .3)'
                },
                {
                    offset: 1,
                    color: 'rgba(44, 119, 244, .3)'
                }
            ]
        }
        return {
            type: 'linear',
            x: 0,
            y: 0,
            x2: 0,
            y2: 1,
            colorStops
        }
    },

    // 提示框
    tooltip: () => {
        let { textPrimary, fontFamily } = utils.variables()
        return {
            formatter: '{b0}: {c0}',
            trigger: 'axis',
            backgroundColor: textPrimary,
            textStyle: {
                fontFamily,
                fontSize: 12
            },
            axisPointer: {
                axis: 'y'
            },
            extraCssText: 'box-shadow: 0 -2px 4px 0 rgba(38,38,38,.2)'
        }
    }
}

export default utils
