import Vue from '@/components/base'
import { Component, Prop } from 'vue-property-decorator'
import template from './Avatar.vue'

@Component({
    name: 'YzAvatar',
    mixins: [template]
})
export default class YzAvatar extends Vue {
    //#region datas ******************************/
    //#endregion datas ***************************/

    //#region props ******************************/
    @Prop({ default: true })
    protected lazy!: boolean

    @Prop({ default: 60 })
    protected size!: number

    @Prop({ default: '' })
    protected alt!: string

    @Prop({ default: '' })
    protected src!: string
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    protected get tip() {
        if (!this.alt) {
            return ''
        }

        return this.alt.substring(0, 1)
    }
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    //#endregion event handler *******************/

    //#region methods ****************************/
    //#endregion methods *************************/
}
