import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'

Vue.use(VueRouter)

let routes: RouteConfig[] = [
    {
        path: '/',
        name: 'home',
        component: () => import('@/views/Home')
    },
    {
        path: '/color',
        name: 'color',
        component: () => import('@/views/BasicColor')
    },
    {
        path: '/font',
        name: 'font',
        component: () => import('@/views/BasicFont')
    },
    {
        path: '/button',
        name: 'button',
        component: () => import('@/views/BasicButton')
    },
    {
        path: '/icon',
        name: 'icon',
        component: () => import('@/views/BasicSvgIcon')
    },
    {
        path: '/input',
        name: 'input',
        component: () => import('@/views/FormInput')
    },
    {
        path: '/select',
        name: 'select',
        component: () => import('@/views/FormSelect')
    },
    {
        path: '/checkbox',
        name: 'checkbox',
        component: () => import('@/views/FormCheckbox')
    },
    {
        path: '/table',
        name: 'table',
        component: () => import('@/views/DataTable')
    },
    {
        path: '/pagination',
        name: 'pagination',
        component: () => import('@/views/DataPagination')
    },
    {
        path: '/notice',
        name: 'notice',
        component: () => import('@/views/NoticeView')
    },
    {
        path: '/tabs',
        name: 'tabs',
        component: () => import('@/views/NavigationTabs')
    },
    {
        path: '/gradient',
        name: 'gradient',
        component: () => import('@/views/GradientView')
    },
    {
        path: '/avatar',
        name: 'avatar',
        component: () => import('@/views/components-view/AvatarView')
    },
    {
        path: '/image',
        name: 'image',
        component: () => import('@/views/components-view/ImageView')
    },
    {
        path: '/charts',
        name: 'charts',
        component: () => import('@/views/components-view/ChartView')
    },
    {
        path: '/upload',
        name: 'upload',
        component: () => import('@/views/components-view/UploadView')
    }
]

let router = new VueRouter({
    routes
})

export default router
