import Vue from '@/components/base'
import { Component, Prop } from 'vue-property-decorator'
import template from './Image.vue'

// @ts-ignore
import supportsWebP from 'supports-webp'

let canSupportsWebP = false
let isInitSupportsWebP = false

const scale = window.devicePixelRatio ? window.devicePixelRatio : 1

/* tslint:disable */
const defaultPlaceholder =
    'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVQYV2M4c+bMfwAIMANkq3cY2wAAAABJRU5ErkJggg=='

@Component({
    name: 'YzImage',
    mixins: [template]
})
export default class YzImage extends Vue {
    //#region datas ******************************/
    protected supportsWebP = false
    //#endregion datas ***************************/

    //#region props ******************************/
    @Prop({ default: '' })
    protected src!: string

    @Prop({ type: Number, default: 80 })
    protected quality!: number

    @Prop({ default: null })
    protected format!: string

    /**
     * 单位 px
     */
    @Prop({ type: [String, Number], default: 100 })
    protected width!: number | string

    /**
     * 单位 px
     */
    @Prop({ type: [String, Number], default: 100 })
    protected height!: number | string

    @Prop({ default: 'Center' })
    protected gravity!: string

    @Prop({ type: [String, Number], default: 1 })
    protected sharpen!: string | number

    @Prop({ type: String, default: null })
    protected blur!: string

    @Prop({ type: Number, default: 0 })
    protected rotate!: number

    @Prop({ default: defaultPlaceholder, type: String })
    protected placeholder!: string
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    protected get currentSrc() {
        if (!this.src) {
            return ''
        }

        if (this.src.indexOf('data:image') === 0) {
            return this.src
        }

        if (this.src.includes('?vframe')) {
            // 如果是视频帧，返回原地址
            return this.src
        }

        let width = (Number(this.width) * scale).toFixed(0)
        let height = (Number(this.height) * scale).toFixed(0)

        let url = `${this.src}?imageMogr2/auto-orient/strip/gravity/${this.gravity}/thumbnail/!${width}x${height}r/crop/${width}x${height}/interlace/1/quality/${this.quality}`

        if (this.blur) {
            url += `/blur/${this.blur}`
        } else if (~~this.sharpen === 1) {
            url += `/sharpen/${this.sharpen}`
        }

        if (this.rotate > 0 && this.rotate < 360) {
            url += `/rotate/${~~this.rotate}`
        }

        if (this.format) {
            url += `/format/${this.format}`
        } else if (this.supportsWebP) {
            url += '/format/webp'
        }

        return url
    }

    protected get imgWidth() {
        return parseInt(String(this.width))
    }

    protected get imgHeight() {
        return parseInt(String(this.height))
    }

    protected get size() {
        let size: { [k: string]: string } = {}

        if (this.width) {
            size.width = this.imgWidth + 'px'
        }
        if (this.height) {
            size.height = this.imgHeight + 'px'
        }
        return size
    }
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected async created() {
        if (isInitSupportsWebP) {
            this.supportsWebP = canSupportsWebP
        } else {
            this.supportsWebP = await supportsWebP
            canSupportsWebP = this.supportsWebP
            isInitSupportsWebP = true
        }
    }
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    //#endregion event handler *******************/

    //#region methods ****************************/
    //#endregion methods *************************/
}
